@media only screen and (min-width: 767px) and (max-width: 998px) {
  .user-dashboard .header-top {
    padding-top: 5px;
  }

  .user-dashboard .header-rightside {
    display: inline-block;
    float: left;
    width: 100%;
  }

  .user-dashboard .header-rightside .header-top img {
    max-width: 41px !important;
  }

  .user-dashboard .welcome-text button {
    font-size: 10px;
  }

  .user-dashboard .btn.btn-secondary.btn-lg.dropdown-toggle > span {
    font-size: 12px;
  }

  .user-dashboard .welcome-text h2 {
    font-size: 16px;
  }
}

.row_dbHome {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.gutter {
  padding: 0;
}

.welcome-text {
  background: #ffffff none repeat scroll 0 0;
  border: 1px solid #d4d9e3;
  display: inline-block;
  padding: 15px;
  width: 100%;
}

