.welcome-text .btn-group {
  float: right;
}

.welcome-text h2 {
  color: #8492af;
  /* float: left; */
  font-size: 21px;
  font-weight: 600;
  margin: 0;
  padding: 0 0 0;
  line-height: 1.5;
}

.welcome-text .btn-group.open .dropdown-toggle,
.btn.active,
.btn:active {
  box-shadow: none;
}

.welcome-text .dropdown-menu > a {
  color: rgb(0, 78, 0);
  display: inline-block;
  font-weight: 800;
  padding: 9px 0;
  text-align: center;
  width: 100%;
}

.welcome-text .dropdown-menu > a:hover {
  color: limegreen;
}

@media only screen and (max-width: 767px) {
  .user-dashboard .welcome-text h2 {
    color: #8492af;
    /* float: left; */
    font-size: 16px;
    font-weight: 600;
    margin: 0;
    padding: 0 0 0;
  }

  .user-dashboard .btn.btn-secondary.btn-lg.dropdown-toggle > span {
    font-size: 11px;
  }

  .user-dashboard .welcome-text button {
    font-size: 11px;
    padding-right: 23px;
  }

  .user-dashboard .welcome-text h2 {
    font-size: 14px;
  }

  .user-dashboard h1 {
    font-size: 25px;
  }

  .gutter {
    padding: 0;
  }
}

.text-justify {
  text-align: justify !important;
}

@media screen and (min-width: 100px) {
  .widthAdjust {
    width: 100%;
  }
}

@media screen and (min-width: 1024px) {
  .user-dashboard h1 {
    font-size: 25px;
  }
}

.linkbutton_db {
  background: none;
  border: none;
  font-weight: 700;
  color: #0d6efd;
}

@media screen and (max-width: 767px) {
  .linkbutton_db {
    font-size: inherit !important;
    padding-right: inherit !important;
  }
}

@media only screen and (min-width: 767px) and (max-width: 998px) {
  .linkbutton_db {
    font-size: inherit !important;
    padding-right: inherit !important;
  }
}
